import React,{useContext} from 'react';
import CartContext from '../../store/cart-Context';
import CartItem from './CartItem';
import Modal from '../UI/Modal';
import styles from './Cart.module.css';

const Cart = props => {
const cartctx = useContext(CartContext);
const addCartHandler = (item) => {
    cartctx.addItem({...item,amount:1});
}
const removeCartHandler = (id) => {
    cartctx.removeItem(id);
}
const totalAmount = `${cartctx.totalAmount}MMK`;
const hasCartItem = cartctx.items.length > 0;
const cartItems = <ul className={styles['cart-items']}> {cartctx.items.map((item)=><CartItem 
                        name = {item.name}
                        id={item.id}
                        key={item.id}
                        price={item.price}
                        amount = {item.amount}
                        addItem = {addCartHandler.bind(null,item)}
                        removeItem = {removeCartHandler.bind(null,item.id)} />)} </ul>
  
    return(
       <Modal onClose = {!props.value && props.onCloseCart}>
            <li>{cartItems}</li>
            <div className={styles.total}>
                <span>Total Amount</span>
                <span>{totalAmount}</span>
            </div>
            <div className={styles.actions}>
                    {!props.value && <button onClick={props.onCloseCart}>Close</button>}
                   {hasCartItem  && <button className={styles['button--alt']} onClick={props.onshowOrder}>Order</button>}
            </div>
        </Modal>
    )
}
export default Cart;