import React,{useState} from 'react';
import CartContextProvider from './store/CartContextProvider';
import Cart from './Components/Cart/Cart';
import Header from './Components/Layout/Header';
import Anime from './Components/accessories/Anime';
import OrderCart from './Components/OrderCart/OrderCart';


function App() {
  const [orderShow,setorderShow] = useState(false);
  const [cartShow,setcartShow] = useState(false);
  const cartShowHandler = () => {
    setcartShow(true);
  }
  const cartCloseHandler = () => {
    setcartShow(false);
  }
  const orderShowHandler = () => {
    setTimeout(()=>{
    setorderShow(true);
    },500);
  }
  const orderCloseHandler = () => {
    setorderShow(false);
    setcartShow(false);
  }
    return (
    <CartContextProvider>
      {orderShow && < OrderCart onClose = {orderCloseHandler}/>}
     {cartShow && <Cart  onCloseCart= {cartCloseHandler} onshowOrder = {orderShowHandler} value={orderShow}/>}
      <Header onCartshow = {cartShowHandler}/>
      <Anime />
    </CartContextProvider>
  );
}

export default App;
