import React,{useReducer} from "react";
import CartContext from './cart-Context';

const defaultValueState = {
    items : [],
    totalAmount : 0,
   }
const cardReducer = (state,action) => {
    if (action.type === 'ADD') {
        const updateTotalAmounts = state.totalAmount + action.item.price * action.item.amount
        const existingcartItemsIndex = state.items.findIndex((item)=> item.id === action.item.id);
        const existingcartItems = state.items[existingcartItemsIndex];
        let updateItems
        if (existingcartItems){
            let updateItem = {
                ...existingcartItems,
                amount : existingcartItems.amount + action.item.amount,
            }
            updateItems = [...state.items]
            updateItems[existingcartItemsIndex] = updateItem;
         }else {
            updateItems = state.items.concat(action.item);
         }
         return{
            items : updateItems,
            totalAmount : updateTotalAmounts,
        }
    }
   return defaultValueState;
}
const CartContextProvider = (props) => {
    const [CartState,disptchCartFun] = useReducer(cardReducer,defaultValueState)
    const addItemHandler = (item) => {
        disptchCartFun({
            item : item,
            type : 'ADD',
        })
    }
    const removeItemHandler = (id) => {
        disptchCartFun({
            id : id,
            type : 'REMOVE',
        })
    }
    const cartItems = {
        items : CartState.items,
        totalAmount : CartState.totalAmount,
        addItem : addItemHandler,
        removeItem : removeItemHandler,
    }
    return(<CartContext.Provider value={cartItems}>
        {props.children}
    </CartContext.Provider>)

}
export default CartContextProvider;