import React,{useContext} from 'react';
import CartContext from '../../store/cart-Context';
import CartIcon from '../Cart/CartIcon';
import styles from './HeaderCartButton.module.css';

const HeaderCartButton = props => {
    const cartItemctx = useContext(CartContext);
    const numberofctx = cartItemctx.items.reduce(
        (cartNum,item) =>{
            return cartNum + item.amount
        },0);
    return(
        <button className={styles.button} onClick={props.onShow}>
            <span className={styles.icon}>
                <CartIcon />
            </span>
            <span className={styles.bump}>Your Cart</span>
            <span className={styles.badge}>{numberofctx}</span>
        </button>
    )
}
export default HeaderCartButton;