import React from "react";
import Card from '../UI/Card';
import AnimeItems from "./AnimeItems/AnimeItems";
import styles from './AvaliableAnime.module.css';

const DUMMY_ANIMEITEMS = [
        {
            id: 'm1',
            name: 'Demon Slayer hoodie',
            description: 'Good Quality',
            price: 21000,
          },
          {
            id: 'm2',
            name: 'Jujutsu Kaisen LED',
            description: 'Good Design',
            price: 12500,
          },
          {
            id: 'm3',
            name: 'Tokyo Revenger',
            description: 'American, raw, meaty',
            price: 57500,
          },
          {
            id: 'm4',
            name: 'One piece Shirt',
            description: 'Preorder',
            price: 24500,
          },
          {
            id: 'm5',
            name: 'One piece Steel necklace',
            description: 'Preorder',
            price: 11000,
          },
          {
            id: 'm6',
            name: 'Naruto Kimono',
            description: 'Preorder',
            price: 24000,
          },
    
]
const AvaliableAnime = props => {
    const animeList = DUMMY_ANIMEITEMS.map((item)=>{
        return(
            <AnimeItems 
                        key={item.id}
                        name={item.name}
                        description={item.description}
                        price={item.price}
                        id={item.id}/>

        )
    })
    return(
        <section className={styles.meals}>
            <Card>
                <ul>{animeList}</ul>
            </Card>
        </section>
    )
}
export default AvaliableAnime;