import React,{useRef} from "react";
import Input from "../../UI/Input";
import styles from './AnimeItemsForm.module.css';

const AnimeItemsFrom = props => {
    const totalAmount = useRef();
    const addClickHandler = (event) =>{
        event.preventDefault();
         const enterAmount = totalAmount.current.value;
         const enterAmountNumber = +enterAmount;
         if(enterAmount.trim().length === 0 || enterAmountNumber < 1 || enterAmountNumber > 5){
             return;
         }
        props.onaddtoCart(enterAmountNumber);
    }
   
    return(
        <form className={styles.form}>
            <Input 
            ref={totalAmount}
                    label='Amount'
                    input = { {
                        id : 'number',
                        type : 'number',
                        defaultValue : '1',
                        min : '1',
                        max : '5',
                        step : '1',
                    }
                    }/>

            <button onClick={addClickHandler}>+Add</button>
        </form>
    )
}
export default AnimeItemsFrom;